import React, {useState, useEffect} from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Registration from './panels/registration';
import Start from './panels/Start';
import Settings from './panels/Settings';
import Load from './panels/Load';
import axios from 'axios';

let App = (props) => {
	let [activePanel, setActivePanel] = useState('load');
	let [fetchedUser, setFetchedUser] = useState({
		id: 124,
		first_name : 'Alexander',
		last_name: 'Zhabovich',
		photo_100: 'https://sun9-3.userapi.com/c639822/v639822055/55abd/Y4nIaWtNYVI.jpg'
	});
	let [user, setUser] = useState(null);
	let [popout, setPopout] = useState(<ScreenSpinner/>)
	useEffect(() => {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					setFetchedUser(e.detail.data)
					console.log('User = ', e.detail.data)
					console.log('UserId = ', e.detail.data.id)
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}, []);
	useEffect(() => {
		if (fetchedUser) axios(`http://localhost:3000/getUserById/${fetchedUser.id}`).then(response => {
			setPopout(null);
			console.log('response = ', response)
			if (response.data.length == 0) {
				setActivePanel('start');
			} else {
				setActivePanel('home');
				setUser(response.data[0])
			}
		})
	}, [fetchedUser])
	let go = (e) => {
		console.log(e.currentTarget.dataset.to)
		setActivePanel( e.currentTarget.dataset.to );
	};
	
		return (
			<View activePanel={activePanel} popout={popout}>
				<Home id='home' go={go} fetchedUser={fetchedUser}/>
				<Registration id="reg"  go={go} fetchedUser={fetchedUser}/>
				<Start id='start' go={go} fetchedUser={fetchedUser}/>
				<Settings id='settings' go={go}/>
				<Load id='load'/>
			</View>
		);
}

export default App;
